<template>
    <div class="order">
            <div class="tag">
                <div class="order_search">
                <el-form :inline="true" :model="formInline" class="demo-form-inline">
                    <el-form-item label="查询方式">
                        <el-select v-model="formInline.region" placeholder="请选择查询方式">
                        <el-option label="订单编号" value="id"></el-option>
                        <el-option label="用户手机号" value="mobile"></el-option>
                        </el-select>
                    </el-form-item>
                    <el-form-item label="内容">
                        <el-input v-model="formInline.searchIup" placeholder="请输入内容"></el-input>
                    </el-form-item>
                    <el-form-item>
                        <el-button type="primary" @click="onSubmit">查询</el-button>
                    </el-form-item>
                </el-form>
            </div>
            <el-tabs v-model="activeName" @tab-click="bindClickType" >
                <el-tab-pane :label="item.title" :name="item.type" v-for="(item,index) in orderBtnType">
                    <el-table
                        :data="tableData5"
                        style="width: 100%">
                        <el-table-column type="expand">
                        <template slot-scope="props">
                            <el-form label-position="left" inline class="demo-table-expand">
                                <el-form-item label="商品编号">
                                    <span>{{ props.row.order_oid }}</span>
                                </el-form-item>
                                <el-form-item label="用户编号">
                                    <span>{{ props.row.user_id }}</span>
                                </el-form-item>
                                <el-form-item label="商品类型">
                                    <span>{{ props.row.comm_type }}</span>
                                </el-form-item>
                                <el-form-item label="商品名称">
                                    <span>{{ props.row.order_name }}</span>
                                </el-form-item>
                                <el-form-item label="房屋面积">
                                    <span>{{ props.row.order_per }}</span>
                                </el-form-item>
                                <el-form-item label="预约时间">
                                    <span>{{ props.row.make_date }}</span>
                                </el-form-item>
                                <el-form-item label="下单时间">
                                    <span>{{ props.row.place_time }}</span>
                                </el-form-item>
                                <el-form-item label="小区地址">
                                    <span>{{ props.row.user_addree }}</span>
                                </el-form-item>
                                <el-form-item label="号楼门牌">
                                    <span>{{ props.row.user_num }}</span>
                                </el-form-item>
                                <el-form-item label="用户姓名">
                                    <span>{{ props.row.user_name }}</span>
                                </el-form-item>
                                <el-form-item label="手机号码">
                                    <span>{{ props.row.user_mobile }}</span>
                                </el-form-item>
                                <el-form-item label="支付方式">
                                    <span v-if="props.row.play_way == 0">微信支付</span>
                                    <span v-if="props.row.play_way == 1">线下支付</span>
                                </el-form-item>
                                <el-form-item label="支付价格">
                                    <span>{{ props.row.order_price }}</span>
                                </el-form-item>
                                <el-form-item label="接单人">
                                    <span>{{ props.row.project }}</span>
                                </el-form-item>
                            </el-form>
                        </template>
                        </el-table-column>
                        <el-table-column
                        label="商品编号"
                        prop="order_oid">
                        </el-table-column>
                        <el-table-column
                        label="下单时间"
                        prop="place_time">
                        </el-table-column>
                        <el-table-column
                        label="商品类型"
                        prop="comm_type">
                        </el-table-column>
                        <el-table-column
                        label="支付方式">
                            <template slot-scope="scope">
                                <span v-if="scope.row.play_way == 0">微信支付</span>
                                <span v-if="scope.row.play_way == 1">线下支付</span>
                            </template>
                        </el-table-column>
                        <el-table-column
                        label="订单状态">
                            <template slot-scope="scope">
                                <span v-if="scope.row.order_type == 1">待付款</span>
                                <span v-if="scope.row.order_type == 2">待完成</span>
                                <span v-if="scope.row.order_type == 3">已完成</span>
                                <span v-if="scope.row.order_type == 4">已取消</span>
                                <span v-if="scope.row.order_type == 5">维权中</span>
                                <span v-if="scope.row.order_type == 6">退款中</span>
                                <span v-if="scope.row.order_type == 7">退款完成</span>
                            </template>
                        </el-table-column>
                        <el-table-column label="操作">
                            <template slot-scope="scope">
                                <el-button
                                size="mini"
                                @click="handleEdit(scope.$index, scope.row.order_oid)">操作</el-button>
                                <el-button
                                size="mini"
                                type="danger"
                                v-if="scope.row.order_type != 3 || scope.row.order_type != 4 || scope.row.order_type != 5"
                                @click="handleDone(scope.$index, scope.row.order_oid,scope.row)">完成</el-button>
                                <el-button
                                size="mini"
                                type="danger"
                                v-if="scope.row.order_type == 6"
                                @click="handleDelete(scope.$index, scope.row)">退款</el-button>
                                <el-button
                                size="mini"
                                type="danger"
                                v-if="roles == 'super_editor'"
                                @click="handleDelete(scope.$index, scope.row)">删除</el-button>
                            </template>
                        </el-table-column>
                    </el-table>
                </el-tab-pane>
            </el-tabs>
            
        </div>
        
        <div class="page">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page="currentPage"
                    :page-sizes="[5,10,15,20]"
                    :page-size="pagesize"
                    :total="total"    
                    layout="total, sizes, prev, pager, next, jumper">    
            </el-pagination>
        </div>
    </div>
</template>

<style lang="scss" scoped>
    .order {
        padding: 20px;
    }
    .demo-table-expand {
        font-size: 0;
    }
    .demo-table-expand label {
        width: 90px;
        color: #99a9bf;
    }
    .demo-table-expand .el-form-item {
        margin-right: 0;
        margin-bottom: 0;
        width: 50%;
    }
    .page {
        padding: 30px 0;
        float: right;
    }
</style>

      
<script>
export default {
    data() {
        return {
            activeName: '0',
            tableData5: [],
            roles:"", // 权限判断参数
            currentPage:1, //初始页
            pagesize:20,    //    每页的数据
            total:3, // 订单列表总数量
            formInline:{
                searchIup:"", // 搜索内容
                region:'',// 搜索类型
            },
            orderBtnType:[
                { cor:"", type:"0", title:"全部订单" },
                { cor:"primary", type:"1", title:"待付款" },
                { cor:"success", type:"2", title:"待完成" },
                { cor:"danger", type:"3", title:"已完成" },
                { cor:"info", type:"4", title:"已取消" },
                { cor:"danger", type:"5", title:"维权中" },
                { cor:"info", type:"6", title:"退款完成" },
            ]
            
        }
    },
    mounted() {
        //this.orderList();   
        this.PageOrderList(); // 分页订单列表
        this.roles = this.$store.state.userInfo.roles;
    },
    methods: {
        bindClickType(tab) {
            let that = this;
            let url = '/api'+this.$common.orderSearchType;
            let data = {
                order_type:tab.name
            }
            that.$http.post(url,data).then((res)=>{
                that.tableData5 = res.data;
            }).catch(data=>{
            })
        },
        // 初始页currentPage、初始每页数据数pagesize和数据data
        handleSizeChange: function (size) {
            this.pagesize = size;
            this.PageOrderList(); // 分页订单列表
            console.log(this.pagesize)  //每页下拉显示数据
        },
        handleCurrentChange: function(currentPage){
            this.currentPage = currentPage;
            this.PageOrderList(); // 分页订单列表
            console.log(this.currentPage)  //点击第几页
        },
        PageOrderList () {
            let that = this;
            let url = '/api'+this.$common.order_page;
            let data = {
                offset:that.currentPage,
                limit:that.pagesize
            }
            that.$http.post(url,data).then((res)=>{
                console.log(res)
                if ( res.code == '200' ) {
                    that.tableData5 = res.data;
                    that.total = res.num;
                }
            }).catch(data=>{
            })
        },
        handleEdit (index,order_oid) {
            let that = this;
            that.$prompt('请输入接单人姓名', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
            }).then(({ value }) => {
                console.log(value)
                let url = '/api'+that.$common.project;
                let data = ({
                    order_oid:order_oid,
                    project:value
                })
                that.$http.post(url,data).then((res)=>{
                    console.log(res);
                    if(res.status == 1){
                        this.PageOrderList(); // 分页订单列表
                        this.$message({
                            message: '增加接单人成功',
                            type: 'success'
                        });   
                    }else {
                        this.$message({
                            message: '已有接单人',
                            type: 'success'
                        });
                    }
                }).catch(res=>{
                    this.$message({
                        message: '增加接单人失败',
                        type: 'success'
                    });
                })
            }).catch(() => {
                      
            });
        },
        handleDone (index,order_oid,order_data) {
            let that = this;
            let openid = order_data.user_id;
            let name = order_data.comm_type;
            let project = order_data.project;
            let price = order_data.order_price;
            this.$confirm('是否已经完成订单', '提示', {
                confirmButtonText: '确定',
                cancelButtonText: '取消',
                type: 'warning'
            }).then(() => {
                let url = '/api'+that.$common.done;
                let data = ({
                    order_oid:order_oid,
                })
                that.$http.post(url,data).then((res)=>{
                    console.log(res);
                    if(res.status == 3){
                        that.$message({
                            message: '该订单已经完成，无须再次操作',
                            type: 'success'
                        });
                    } else if (res.status == 1){
                        this.PageOrderList(); // 分页订单列表
                        that.$message({
                            message: '完成订单',
                            type: 'success'
                        });
                    }
                }).catch(res=>{
                    this.$message({
                        message: '完成订单失败',
                        type: 'success'
                    });
                })   
            }).catch(() => {
                          
            });
            
        },
        onSubmit () {
           
            let that = this;
            let url;
            let data = ({
                order_oid:that.formInline.searchIup,
            })
            if (that.formInline.region == 'id') {
                url = '/api'+that.$common.orderSearchId;
            } else if (this.formInline.region == 'mobile') {
                url = '/api'+that.$common.orderSearchMobile;
            }
            if (that.formInline.searchIup == '') {
               this.PageOrderList(); // 分页订单列表
            } else {
                that.$http.post(url,data).then((res)=>{
                    console.log(res);
                    if(res.status == 1){               
                        this.tableData5 = res.data;
                    }
                }).catch(res=>{
                    this.$message({
                        message: '搜索失败',
                        type: 'success'
                    });
                })
            }
            
        }
    },
}
</script>


